<script>
  let imgs = {
    logo: "images/logo.svg",
    ellipse_1: "images/ellipse_1.svg",
    avatar: "images/avatar.svg",
    message: "images/feather_message-circle.svg",
    arrow_down: "images/arrow-down.svg",
    decoration: "images/decoration.svg",
    glyde_frame: "images/glyde_frame.png",
    simresolvemos_frame: "images/simresolvemos_frame.png",
    clients: "images/clients.svg",
    footer_bg: "images/footer_bg.png",
    github: "images/github.svg",
    gmail: "images/gmail.svg",
    linkedin: "images/linkedin.svg",
  };

  export let extraClass = "";
  export let background = "bg-gradient-to-br from-fiery-rose to-transparent";
  export let title = "Project";
  export let technologies = ["Tech"];
  export let img = "";
  export let url = "https://www.glyde.com.br";

  const handleClick = () => {
    window.open(url, "_blank");
  };
</script>

<style type="text/scss">
  .project-card {
    img {
      transition: 0.5s ease;
    }

    &:hover {
      cursor: pointer;
      img {
        transform: scale(1.025);
      }
    }
  }
</style>

<main class={`project-card flex-1 ${extraClass}`} on:click={handleClick}>
  <div
    class={`${background} rounded-lg mr-0
    sm:mr-5 pl-6 pt-6 overflow-hidden`}>
    <div
      class="flex justify-between items-start lg:items-center flex-col
      lg:flex-row ">
      <h2 class="font-body font-bold text-3xl text-left">{title}</h2>
      <div class="flex items-center mr-6">
        {#each technologies as tech, idx}
          <span
            class={`bg-ghost-white bg-opacity-25 rounded-md px-2 py-1 ${idx !== technologies.length - 1 ? 'mr-2' : ''}`}>
            {tech}
          </span>
        {/each}
      </div>
    </div>
    <img src={img} alt={`${title} project`} class="ml-16 mt-24" />
  </div>
</main>
